.App {
  height: 650px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  
  
  background-color: #24325a;
}
.bg{
  height: 400px;
  width: 400px;
  text-align: center;
  border: 2px solid brown;
  border-radius: 20px;
  background-color: rgba(50, 255, 187, 0.92);
  /* background-image: url(./lolo.png); */
} 
.lastline{
  font-size: xx-large;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-weight: 700;
  background-color: rgb(170, 255, 255);
  color: rgb(0, 0, 0);
}
input{
  height: 20px;
  width: 50%;
} 
button{
height: 50px;
width: 60px;
text-align: center;
font-weight: 700;
font-style: oblique;
font: size 10px;
background-color: blue;
color: white;
border-radius: 20px;
}


  
 @media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
} 


